<template>
  <div class="app-container">
    <div class="filter-container">
      <!-- @keyup.enter.native="handleFilter" -->
      <el-input v-model="listQuery.examName" placeholder="" style="width: 200px;" class="filter-item" @keyup.enter.native="handleQuery" />
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="handleQuery">
        
      </el-button>
      <el-button class="filter-item" style="margin-left: 10px;" type="primary" icon="el-icon-edit" @click="addExam">
        Add
      </el-button>
    </div>
    <el-table v-loading="listLoading" :data="examList" :expand-row-keys="expands" :row-key="getRowKeys" element-loading-text="Loading" border fit highlight-current-row max-height="625" @expand-change="expandChange">
      <el-table-column type="expand">
        <template slot-scope="{row}">
          <div class="open-diag">
            <!-- <div class="content-item">
              <div class="title">
                
              </div>
              <div class="words">
                {{ row.ExamId }}
              </div>
            </div> -->
            <div class="content-item">
              <div class="title">
                
              </div>
              <div class="words">
                {{ row.createTime | parseTime }}
              </div>
            </div>
            <div class="content-item">
              <div class="title">
                
              </div>
              <div class="words">
                {{ row.updateTime | parseTime }}
              </div>
            </div>
            <div class="content-item">
              <div class="title">
                
              </div>
              <div class="words">
                {{ row.description }}
              </div>
            </div>
            <div class="content-item">
              <div class="title">
                
              </div>
              <div class="words">
                {{ row.announcements }}
              </div>
            </div>
            <el-button type="primary" size="mini" @click="exportRegisterInfo(row)">
              
            </el-button>
            <el-upload action="http://localhost:2020/report/read" style="margin-top:20px">
              <el-button size="small" type="primary">
                
              </el-button>
            </el-upload>
          </div>
        </template>
      </el-table-column>
      <el-table-column align="center" type="index" />
      <el-table-column align="center" label="" prop="title" />
      <el-table-column align="center" label="" prop="startTime">
        <template slot-scope="{row}">
          <span>
            {{ row.startTime | parseTime }}
          </span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" prop="endTime">
        <template slot-scope="{row}">
          <span>
            {{ row.endTime | parseTime }}
          </span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" prop="status.msg">
        <template slot-scope="{row}">
          <el-tag v-if="row.status && row.status.enumCode === 'NotStarted'">
            {{ row.status.msg }}
          </el-tag>
          <el-tag v-if="row.status &&row.status.enumCode === 'Start'" type="success">
            {{ row.status.msg }}
          </el-tag>
          <el-tag v-if="row.status &&row.status.enumCode === 'Stop'" type="warning">
            {{ row.status.msg }}
          </el-tag>
          <el-tag v-if="row.status &&row.status.enumCode === 'Score_Inquiry'" type="danger">
            {{ row.status.msg }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" prop="price" />
      <el-table-column align="center" label="" prop="applyStartTime">
        <template slot-scope="{row}">
          <span>
            {{ row.applyStartTime | parseTime }}
          </span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" prop="applyEndTime">
        <template slot-scope="{row}">
          <span>
            {{ row.applyEndTime | parseTime }}
          </span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" prop="userId" />
      <el-table-column align="center" label="" prop="examType.msg" />
      <!-- <el-table-column align="center" label="" prop="createTime">
        <template slot-scope="{row}">
          <span>
            {{ row.createTime | parseTime }}
          </span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" prop="updateTime">
        <template slot-scope="{row}">
          <span>
            {{ row.updateTime | parseTime }}
          </span>
        </template>
      </el-table-column> -->
      <el-table-column align="center" label="" width="140">
        <template slot-scope="row">
          <el-button type="primary" size="mini" @click="handleUpdate(row)">
            
          </el-button>
          <el-popconfirm title="" @confirm="handleDelete(row)">
            <el-button slot="reference" size="mini" type="danger">
              
            </el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.pageNum" :limit.sync="listQuery.pageSize" @pagination="getExamList" />

    <!--  -->
    <el-dialog :visible.sync="dialogFormVisible" :title="type === 'add' ? '' : ''">
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" label-width="130px" style="width: 400px; margin-left:50px;">
        <el-form-item label="" prop="title">
          <el-input v-model="temp.title" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="temp.description" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="applyStartTime">
          <el-date-picker v-model="temp.applyStartTime" type="datetime" placeholder="" @change="applyStartTimeChange" />
        </el-form-item>
        <el-form-item label="" prop="applyEndTime">
          <el-date-picker v-model="temp.applyEndTime" type="datetime" placeholder="" @change="applyEndTimeChange" />
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="temp.price" type="number" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="startTime">
          <el-date-picker v-model="temp.startTime" type="datetime" placeholder="" @change="startTimeChange" />
        </el-form-item>
        <el-form-item label="" prop="endTime">
          <el-date-picker v-model="temp.endTime" type="datetime" placeholder="" @change="endTimeChange" />
        </el-form-item>
        <el-form-item label="" prop="announcements">
          <el-input v-model="temp.announcements" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="examType">
          <el-select v-model="temp.examType" class="filter-item" placeholder="">
            <el-option v-for="item in examTypeEnum" :key="item.enumCode" :label="item.msg" :value="item.enumCode" />
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="" prop="status">
          <el-select v-model="temp.status" class="filter-item" placeholder="">
            <el-option v-for="item in examStatusEnum" :key="item.enumCode" :label="item.msg" :value="item.enumCode" />
          </el-select>
        </el-form-item> -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">
          
        </el-button>
        <el-button type="primary" @click="dialogStatus==='create'?addData():updateData()">
          
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Pagination from '@/components/Pagination'
import { parseTime } from '@/utils/time'

export default {
  components: { Pagination },
  filters: {
    parseTime (val) {
      return parseTime(val, '{y}-{m}-{d} {h}:{i}') || '--'
    }
  },
  data () {
    const checkApplyStartTime = (rule, value, callback) => {
      if (value <= new Date()) {
        callback(new Error(''))
      }
    }
    const checkApplyEndTime = (rule, value, callback) => {
      if (value <= this.temp.applyStartTime) {
        callback(new Error(''))
      }
    }
    const checkPrice = (rule, value, callback) => {
      if (typeof +value !== 'number') {
        callback(new Error('0'))
      } else {
        if (+value < 0) {
          callback(new Error('0'))
        }
      }
    }
    const checkStartTime = (rule, value, callback) => {
      if (value <= this.temp.applyEndTime + 172800000) {
        callback(new Error('2'))
      }
    }
    const checkEndTime = (rule, value, callback) => {
      if (value <= this.temp.startTime) {
        callback(new Error(''))
      }
    }
    return {
      type: 'add',
      expands: [],
      listQuery: {
        pageNum: 1,
        pageSize: 10,
        examName: ''
      },
      listLoading: true,
      dialogFormVisible: false,
      dialogStatus: '',
      temp: {
        // id: '',
        description: '',
        // status: '',
        title: '',
        examType: '',
        price: '',
        announcements: '',
        startTime: null,
        endTime: null, // 
        applyStartTime: null,
        applyEndTime: null
      },
      visible: false,
      total: 0,
      rules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'change' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' },
          { max: 200, message: '200', trigger: 'change' }
        ],
        applyStartTime: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkApplyStartTime, trigger: 'change' }
        ],
        applyEndTime: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkApplyEndTime, trigger: 'change' }
        ],
        price: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPrice, trigger: 'blur' }
        ],
        startTime: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkStartTime, trigger: 'change' }
        ],
        endTime: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEndTime, trigger: 'change' }
        ],
        announcements: [
          { required: true, message: '', trigger: 'blur' },
          { max: 200, message: '200', trigger: 'change' }
        ],
        examType: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapState({
      examList: state => state.exam.examList,
      roleList: state => state.enumList.roleList,
      examTypeEnum: state => state.enumList.examTypeEnum,
      examStatusEnum: state => state.enumList.examStatusEnum
    })
  },
  mounted () {
    this.getExamList()
    this.$store.dispatch('getExamTypeEnum', 'ExamTypeEnum')
    this.$store.dispatch('getExamStatusEnum', 'ExamStatusEnum')
  },
  methods: {
    /**  */
    startTimeChange (e) {
      this.temp.startTime = e.getTime()
    },
    /**  */
    endTimeChange (e) {
      this.temp.endTime = e.getTime()
    },
    /**  */
    applyStartTimeChange (e) {
      this.temp.applyStartTime = e.getTime()
    },
    /**  */
    applyEndTimeChange (e) {
      this.temp.applyEndTime = e.getTime()
    },
    // 
    getRoleList () {
      this.$store.dispatch('getExamRoleList', 'ExamRoleEnum')
    },

    // 
    getExamList () {
      const params = {
        pageNum: this.listQuery.pageNum,
        pageSize: this.listQuery.pageSize
      }
      this.$store.dispatch('getExamList', params).then(res => {
        this.total = res.total
        this.listLoading = false
        console.log(this.examList)
      }).catch(err => {
        console.log(err)
      })
    },

    // form
    resetrForm () {
      this.temp = {
        // id: '',
        description: '',
        // status: '',
        title: '',
        examType: '',
        price: '',
        announcements: '',
        startTime: null,
        endTime: null, // 
        applyStartTime: null,
        applyEndTime: null
      }
    },

    // 
    addExam () {
      this.type = 'add'
      this.resetrForm()
      this.dialogFormVisible = true
      this.dialogStatus = 'create'
    },
    // 
    handleUpdate (row) {
      this.type = ''
      this.temp = Object.assign({}, row.row)
      console.log(row.row)
      // this.temp.id = row.row.id
      // this.temp.title = row.row.title
      // this.temp.description = row.row.description
      this.temp.examType = row.row.examType.enumCode
      this.temp.status = row.row.status.enumCode
      // this.temp.price = row.row.price
      // this.temp.announcements = row.row.announcements
      this.dialogFormVisible = true
      this.dialogStatus = 'update'
    },

    // 
    handleDelete (row) {
      this.$store.dispatch('deleteExam', row.row.id).then(res => {
        this.getExamList()
        this.$notify({
          title: 'Success',
          message: '',
          type: 'success',
          duration: 2000
        })
      }).catch(err => {
        this.$message.error(err)
      })
    },

    // 
    addData () {
      this.$store.dispatch('addExam', this.temp).then(res => {
        this.getExamList()
        this.$notify({
          title: 'Success',
          message: '',
          type: 'success',
          duration: 2000
        })
        this.dialogFormVisible = false
      }).catch(err => {
        console.log(err)
        // this.$message.error(err)
      })
    },
    // 
    updateData () {
      this.$store.dispatch('updateExam', this.temp).then(res => {
        this.getExamList()
        this.$notify({
          title: 'Success',
          message: '',
          type: 'success',
          duration: 2000
        })
        this.dialogFormVisible = false
      }).catch(err => {
        this.$message.error(err)
      })
    },

    // 
    handleQuery () {
      const params = {
        pageNum: this.listQuery.pageNum,
        pageSize: this.listQuery.pageSize,
        condition: { title: this.listQuery.examName }
      }
      console.log(params)
      console.log(params.condition)
      this.$store.dispatch('queryExam', params)
    },

    getRowKeys (row) {
      return row.id
    },

    expandChange (row, expandedRows) {
      const that = this
      console.log(expandedRows)
      if (expandedRows.length) { // 
        that.expands = []
        if (row) {
          that.expands.push(row.id)// id
        }
      } else { // 
        that.expands = []
      }
    },

    /*  */
    exportRegisterInfo (row) {
      console.log(row.id)
      this.$store.dispatch('exportRegisterInfo', row.id).then(res => {
        console.log(res)
        const content = res.data
        // const contentDisposition = res.headers['Content-Disposition'] // responseheadersfilename, response.setHeader("content-disposition", "attachment; filename=xxxx.docx") ;
        // console.log(contentDisposition)
        // const patt = new RegExp('filename=([^;]+\\.[^\\.;]+);*')
        // const result = patt.exec(contentDisposition)
        // console.log(result)
        // const filename = decodeURI(result[1])
        const filename = `${row.title}.xls`
        // a
        const elink = document.createElement('a')
        // 
        elink.download = filename
        elink.style.display = 'none'
        const blob = new Blob([content])
        elink.href = URL.createObjectURL(blob)
        document.body.appendChild(elink)
        elink.click()
        document.body.removeChild(elink)
      })
    }
  }

}
</script>
<style lang="scss" scoped>
.content-item {
  display: flex;
  margin-bottom: 20px;
  .title {
    margin-right: 6 px;
    font-weight: 600;
    font-size: 18px;
  }
  .words {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-style: 16px;
  }
}
</style>
